*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;

}

.header{
    background-color: rgb(175, 139, 175);
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 60px;
}

.header a{
text-decoration: none;
}
.cart_no{
    background-color: black;
    color: #fff;
    border-radius: 50%;
    width: 3rem;
    height: 3rem;
    margin: 2px;
    padding: 2px 6px;
}

.redirtsingle{
    text-decoration: none;
    color: #000;
}
.header li{
    list-style: none;
    font-weight: bolder;

}
.btn{
    text-decoration: none;
    border: 2px solid black;
    border-radius: 20px;
    padding: 10px 30px;
    transition: all 0.3s ease-in;
    
}
.btn:hover{
    background-color: rgba(29, 30, 29, 0.484);
    color: #fff;
    border: 2px solid rgba(32, 37, 32, 0.646);
    
}
.page{
    display: flex;
}

.image{
    margin: 100px 0 0 50px;
}
.image img{
    width: 400px;
   
}
.datile{
    margin-top: 200px;
    margin-left: 100px;
    
}
/* .datial,h2,p,a{
    margin: 60px 0;
} */
.datile a{
    padding: 10px 100px;

}
.header li a{
    text-decoration: none;
    font-size: larger;
}

.box {
    display: flex;
    height: 200px;
    width: 100vw;
    flex-wrap: wrap;
    margin: 50px;

    
}

.itembox{
    margin: 30px 20px;
    /* border: 2px solid red; */
    width: 250px;
}

.itembox img {
    height: 150px;


}
.itembox h3{
    text-align: center;
}
.itembox p {
    width: 150px;
    height: 90px;
    /* border: 2px solid blue; */
    padding: 10 0px;
}

.itembox button{
    width: 100%;
    height: 50px;
    border-radius: 30px;
}


.cartImage{
    border: 1px solid rgba(0, 0, 0, 0.541);
    width: 120px;
    height: 140px;
    
}
.cart_item{
    display:grid;
    grid-template: 180px 1fr /150px 2fr ;
margin: 10px;
    column-gap: 10px ;

    
}
.product_detail{
display: flex;
flex-direction: column;
}
.product_detail input{
    width: max-content;

}
.empty_cart{
    display: grid;
    place-items: center;
    width: 100%;
    height: 95vh;
    background-color: #f7f7f7ff;
}
 .about{
    padding: 3rem;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
    font-size: large;
    flex-direction: column;
    height: 91vh;
    background-color:rgba(85, 175, 197, 0.436);
    /* text-align: center; */
}
.contact {
    padding: 3rem;
    display: flex;
    justify-content: center;
}
.contact h1 , .about h1 {
    border-bottom: 5px solid rgba(60, 57, 57, 0.688);
  border-radius: 5px;
}
.total{
    border-top :2px solid rgba(34, 32, 32, 0.685); 
    display: flex;
    justify-content: space-between;
    text-align: right;
    padding: 10px 20px 20px 20px;

}
.active {

    color: rgb(44, 51, 51);
}
